/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
  color: #fff;
  font-family: "Exo", sans-serif;
  font-size: 1rem;
  line-height: 1.5; }

/* Headings */
h1, h2, h3, h4, h5, h6 {
  clear: both; }

.entry-title h2 {
  display: inline-block; }

/* Copy */
p {
  margin-bottom: 1.5rem; }

dfn, cite, em, i {
  font-style: italic; }

blockquote {
  margin: 0 1.5rem; }

address {
  margin: 0 0 1.5rem; }

pre {
  background: #eee;
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: .9375rem;
  line-height: 1.6;
  margin-bottom: 1.6rem;
  max-width: 100%;
  overflow: auto;
  padding: 1.6rem; }

code, kbd, tt, var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: .9375rem; }

abbr, acronym {
  border-bottom: 1px dotted #666;
  cursor: help; }

mark, ins {
  background: #fff9c0;
  text-decoration: none; }

big {
  font-size: 125%; }

/*--------------------------------------------------------------
# Elements
--------------------------------------------------------------*/
html {
  box-sizing: border-box;
  font-size: 16px; }

*,
*:before,
*:after {
  /* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
  box-sizing: inherit; }

body {
  background: #222222;
  font-family: "Exo", sans-serif;
  color: #fff;
  font-size: 1rem;
  line-height: 1.5; }

p {
  width: 100%;
  text-decoration: none; }

blockquote, q {
  quotes: "" ""; }
  blockquote:before, blockquote:after, q:before, q:after {
    content: ""; }

hr {
  background-color: #ccc;
  border: 0;
  height: 1px;
  margin-bottom: 1.5rem; }

ul, ol {
  margin: 0 0 1.5rem 0;
  width: 100%; }

ul {
  list-style: none; }

ol {
  list-style: decimal; }

li > ul,
li > ol {
  margin-bottom: 0;
  margin-left: 0;
  width: 100%; }

dt {
  font-weight: bold; }

dd {
  margin: 0 1.5rem 1.5rem; }

img {
  height: auto;
  /* Make sure images are scaled correctly. */
  max-width: 100%;
  /* Adhere to container width. */ }

table {
  margin: 0 0 1.5rem;
  width: 100%; }

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
/* Submit a Quote Page */
.form {
  display: flex;
  flex-flow: column nowrap;
  margin: 1rem 0; }
  .form label {
    margin: .5rem 0; }
  .form .quote-author, .form .quote-source, .form .quote-source-url {
    width: 65%; }
    @media (min-width: 1080px) {
      .form .quote-author, .form .quote-source, .form .quote-source-url {
        width: 50%; } }
  .form .quote-content {
    width: 100%; }

.quote-submission-wrapper .submit-success.message {
  display: inline-block; }

/* Buttons */
.single-post button {
  margin: 5rem; }
  @media (min-width: 1080px) {
    .single-post button {
      margin: 0 auto;
      width: 100%; } }

.error404 .search-submit {
  border: none;
  padding: .5rem .25rem;
  width: 10%; }

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  border: 1px solid #00cc00;
  border-radius: 3px;
  background: #222222;
  color: #00cc00;
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1;
  padding: .5rem 1rem;
  width: 50%; }
  @media (min-width: 620px) {
    button,
    input[type="button"],
    input[type="reset"],
    input[type="submit"] {
      width: 40%;
      max-width: 350px; } }
  button:hover,
  input[type="button"]:hover,
  input[type="reset"]:hover,
  input[type="submit"]:hover {
    border-color: #00cc00; }

/* Fields */
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
textarea {
  color: #fff;
  background-color: #222222;
  border: 1px solid #00cc00;
  border-radius: 3px; }
  @media (min-width: 620px) {
    input[type="text"],
    input[type="email"],
    input[type="url"],
    input[type="password"],
    input[type="search"],
    textarea {
      font-size: 1.25rem; } }

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"] {
  padding: 3px; }

textarea {
  padding-left: 3px;
  width: 100%; }

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
/* Links */
a {
  color: #00cc00;
  text-decoration: none; }
  a:visited {
    color: #00cc00; }
  a:hover, a:active {
    color: #009900; }
  a:hover, a:active {
    outline: 0; }

/* Menus */
.main-navigation {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  width: 100%; }
  @media (min-width: 1080px) {
    .main-navigation {
      justify-content: flex-end; } }
  .main-navigation p {
    display: inline-block;
    flex-basis: 100%; }
    @media (min-width: 620px) {
      .main-navigation p {
        flex-basis: 0;
        margin: 0;
        padding-left: 1rem;
        word-wrap: none;
        width: 100%;
        min-width: 280px; } }
  .main-navigation ul {
    display: flex;
    flex-flow: row wrap;
    list-style: none;
    margin: 0;
    padding-left: 0; }
    .main-navigation ul li:first-child {
      padding-left: 0; }
    .main-navigation ul li:last-child {
      border-right: none; }
    @media (min-width: 620px) {
      .main-navigation ul li:last-child {
        border-right: 1px solid #fff; } }
    .main-navigation ul li {
      border-right: 1px solid #fff;
      padding: 0 1rem; }
    .main-navigation ul li:hover > ul,
    .main-navigation ul li.focus > ul {
      left: auto; }
    .main-navigation ul a {
      display: block;
      text-decoration: none; }

@media screen and (min-width: 768px) {
  .menu-toggle {
    display: none; } }

.site-main .comment-navigation, .site-main
.posts-navigation, .site-main
.post-navigation {
  margin: 0 0 1.5rem;
  overflow: hidden; }

.comment-navigation .nav-previous,
.posts-navigation .nav-previous,
.post-navigation .nav-previous {
  width: 50%; }

.comment-navigation .nav-next,
.posts-navigation .nav-next,
.post-navigation .nav-next {
  text-align: right;
  width: 50%; }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    background-color: #f1f1f1;
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    color: #21759b;
    display: block;
    font-size: .875rem;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/* All Single Quote Pages and Archive Pages*/
.archive .entry-content p, .blog .entry-content p, .single .entry-content p {
  font-size: 1.25rem; }
  @media (min-width: 620px) {
    .archive .entry-content p, .blog .entry-content p, .single .entry-content p {
      font-size: 1.75rem; } }
  @media (min-width: 1080px) {
    .archive .entry-content p, .blog .entry-content p, .single .entry-content p {
      font-size: 2.5rem; } }

.entry-title {
  display: inline; }

.source {
  font-size: 1.5rem;
  height: 1.5rem; }

.single-post .post {
  margin-bottom: 5vh; }

/* Archive Page */
.category-archives ul, .post-archives ul, .tag-archives ul {
  margin-left: 0;
  padding-left: 0; }

.archive-lists, .wp-tag-cloud {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start; }

.tag-archives ul li, .category-archives ul li, .post-archives ul li {
  margin-bottom: .5rem;
  margin-right: 1.25rem; }

/* Posts and pages */
.tag .page-header, .tag .page-header, .category .page-header, .category .page-header {
  border-bottom: 1px dashed #fff; }

.tag .hentry, .category .hentry {
  margin: 0 0 1.5rem;
  padding: 0 0 1.5rem;
  border-bottom: 1px dashed #fff; }

.tag .entry-title, .tag .entry-content, .category .entry-title, .category .entry-content {
  font-weight: 300; }

.tag .page-numbers, .category .page-numbers {
  margin: 0 .5rem 0 0; }

.sticky {
  display: block; }

.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5rem 0 0; }

.page-links {
  clear: both;
  margin: 0 0 1.5rem; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/
/* All Pages */
.site {
  padding: 2.5%;
  width: 100%; }
  .site .site-content {
    margin: 7.5vh 0; }
    @media (min-width: 1080px) {
      .site .site-content {
        display: flex;
        flex-flow: row nowrap;
        justify-content: center; }
        .site .site-content:before, .site .site-content:after {
          font-family: 'FontAwesome';
          font-size: 125px; }
        .site .site-content:before {
          content: '\f10d';
          align-self: flex-start;
          color: #00cc00; }
        .site .site-content:after {
          content: '\f10e';
          align-self: flex-start;
          color: #00cc00; } }
    @media (min-width: 1080px) {
      .site .site-content .content-area {
        flex-basis: 60%;
        margin: 0 5vw; } }

/* Footer on all pages */
.site-footer {
  padding: 5% 0; }
  @media (min-width: 1080px) {
    .site-footer {
      padding: 0 5% 0 0; } }

.hero {
  width: 50vw;
  max-height: 100%;
  margin: 10vh 0 0;
  display: block; }
  @media (min-width: 1080px) {
    .hero {
      margin: 0 auto; } }
  .hero img {
    width: 50vw;
    min-width: 300px;
    height: 100%; }
    @media (min-width: 620px) {
      .hero img {
        min-width: 500px; } }

/* 404 error page */
#fieldset {
  border: none;
  margin: 0 2px;
  padding: .5rem .5rem; }

.search-submit {
  width: 10vw; }
